import React from 'react';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import AdminServices from 'services/api/AdminServices';
import { RiskFormat, RiskTextColor } from 'views/mobileView/components/atoms/RiskFunctions';
import cssVar from 'views/mobileView/styles';
import { toast } from 'react-toastify';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

export default function RiskResult(props) {

	const [riskScore, setRiskScore] = React.useState();
	const [loading, setLoading] = React.useState(true);
	const STATE = props.location.state

	const riskText = [
		'You hate taking risks with your money. Invest in safe and portfolios heavy on debt instruments.',
		'You like to stay away from big risks. Equity investing is acceptable only if you are investing for the long term.',
		'You don’t mind taking risks if they are likely to result in good returns. Invest in well-diversified portfolios and you should have a great investing experience.',
		'You love to see high returns on your investments and you don’t mind taking  risks to generate high returns',
		'For you, there is no cost too high to generate high returns. If you think that by investing in an instrument there is even a little chance of generating high returns, you will invest in it.'
	]
	React.useEffect(() => {
		setRiskScore(0)
		if (STATE.riskScore) {
			setRiskScore(STATE.riskScore)
			setLoading(false)
		} else {
			db.get(db.model.user).then((p) => {
				getRiskScore(p.userData.id)
			})
		}
	}, [])

	const getRiskScore = async (id) => {
		const data = {
			"clientId": id,
			"currentAge": Number(STATE.age),
			"noDependents": Number(STATE.dependents || 0),
			"annualIncome": Number(STATE.income || 0),
			"emergencyFund": Number(STATE.emergencyFund || 0),
			"insuranceCover": Number(STATE.lifeInsurance || 0),
			"isDependentInsured": Number(STATE.healthInsurance || 0),
			"gameInvestment": Number(STATE.willingnessQuestion1),
			"planofAction": Number(STATE.willingnessQuestion2),
		}
		console.log(data)
		let res = await AdminServices.getRiskScore(data).then((res) => JSON.stringify(res));
		res = await JSON.parse(res);
		if (res.type === true) {
			console.log("Get Risk API: ", res.data)
			setRiskScore(Number(res.data.RiskScore))
			db.remove(db.model.clientData)
			setLoading(false)
		} else {
			toast.error('Error')
			setLoading(false)
		}
	}

	const CustomProgressBar = () => {
		return (<>
			<style>
				{`
					@keyframes animate {
						from   {background-color: var(--light)}
						to  {background-color: ${RiskTextColor(riskScore)}}
					}
					div.box {
						width: 24px;
						height: 24px;
						margin-right: 10px;
						animation-duration: 0.2s;
						animation-fill-mode: forwards;
						background-color: var(--light)
					}
					@keyframes showAfterAnimate {
						99% { visibility: hidden;}
						100% { visibility: visible;}
					}
					.showAfterAnimate{
						animation-name: showAfterAnimate;
						animation-fill-mode: forwards;
						animation-delay: ${riskScore * 0.2 + 1}s;
						visibility: hidden;
					}
				`}
			</style>
			<div style={{ display: 'flex' }}>
				<div className='box' style={{ animationName: riskScore >= 1 ? 'animate' : '', animationDelay: '0s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 1 ? 'animate' : '', animationDelay: '0.2s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 2 ? 'animate' : '', animationDelay: '0.4s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 2 ? 'animate' : '', animationDelay: '0.8s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 3 ? 'animate' : '', animationDelay: '1s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 3 ? 'animate' : '', animationDelay: '1.2s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 4 ? 'animate' : '', animationDelay: '1.4s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 4 ? 'animate' : '', animationDelay: '1.6s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 5 ? 'animate' : '', animationDelay: '1.8s' }}></div>
				<div className='box' style={{ animationName: riskScore >= 5 ? 'animate' : '', animationDelay: '2s' }}></div>
			</div>
		</>)
	}
	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={100} className="progress-bar-custom" />
			<div className="dataContainer" style={{ paddingTop: '42px' }}>
				{
					loading ?
						<ActivityLoaderMintbox />
						:
						<>
							<div className='header1dark' style={{ marginBottom: '8%' }}>
								We have identified your risk profile as
							</div>
							<CustomProgressBar />
							<div className='header1dark showAfterAnimate' style={{ color: RiskTextColor(riskScore), marginTop: '4%' }}>{RiskFormat(riskScore)}</div>
							<div className='smallTextLight showAfterAnimate' style={{ marginTop: "2%" }}>{riskText[riskScore - 1]}</div>
						</>
				}
			</div>
			<div className="bodyTextLight showAfterAnimate" style={{ position: "fixed", bottom: '70px', width: "100%", borderTop: "1px solid " + cssVar.getPropertyValue('--light'), padding: "2% 4%" }}>
				Please continue to the last step of selecting your investment horizon for wealth creation
			</div>
			<div className='footer'>
				<button className='outlineButton'
					onClick={() => props.history.push({ pathname: "/risk-profile/1" })}>
					Start again
				</button>
				<button className='solidButton'
					onClick={() => props.history.push({ pathname: "/risk-profile/risk-horizon", state: { ...STATE, riskScore } })}>
					Continue
				</button>
			</div>
		</div>
	)
}
